.warning-lead {
  background-color: rgb(212, 30, 30);
  height: auto;
  width: 100%;
  padding: 0.5rem;
  box-sizing: border-box;
  z-index: 999;
  text-align: center;
}

.warning-lead span {
  color: white;
  font-size: 1rem;
}

.margin-top-bottom {
  margin: 1rem 0;
}


.text-white {
  color: white;
}

.main-headline {
  text-align: center;
  font-size: 2rem;
  margin: 1rem;
}

.pagination {
  flex-wrap: wrap;
  display: flex, -ms-flexbox;
}

.footer {
  color: whitesmoke;
  background-color: #080808;
  margin-top: 1rem;
  padding: 2rem;
}

.footer li{
  list-style: none;
}

.footer li a {
  text-decoration: none;
  color: rgb(136, 136, 136);
}

.update-box {
  background: whitesmoke;
  padding: 1rem;
  border-radius: 0.25em;
}

.update-box .update-title {
  font-size: 2rem;
}

.update-box .update-text {
  font-size: 1rem;
}

.footer-bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4rem;
  background-color: #181818;
  color: white;
}

.small-box {
  background-color: rgb(245, 245, 245);
  border-radius: 0.3em;
  padding: 2rem;
  margin: 0.5rem;
}

.uk-label-danger {
  background-color: #f0506e !important;
  color: #fff !important;
}

.table-head {
  width:100%;
}

.map-container {
  width: 100%;
  height: 400px;
}